<template>
  <div class="gdt">
    <img
      src="https://wm-front-common.oss-cn-beijing.aliyuncs.com/wm-front-landing-page-mobile-custom/assets/Scene/Live12/headerLive.png"
      alt="" />
    <div class="formPay">
      <div class="formTips">- 开课3天不满意无条件退款 -</div>
      <VanForm
        @submit="onSubmit"
        @failed="showErrorMsg"
        ref="form"
        validate-first
        :show-error="false"
        :show-error-message="false">
        <VanField
          v-model="form.phone"
          name="phone"
          ref="phone"
          type="tel"
          placeholder="请填写手机号"
          @input="iptPhone"
          :rules="[
            { required: true, message: '33003' },
            {
              pattern: phoneReg,
              message: '33004',
            },
          ]">
          <template #left-icon>
            <span class="prefix"> +86 <span class="icon">▼</span> </span>
          </template>
        </VanField>
        <VanField
          v-show="isShowCode"
          v-model="form.code"
          name="code"
          ref="code"
          type="digit"
          placeholder="请填写验证码"
          :rules="[{ required: true, message: '33005' }]">
          <template #button>
            <div class="code">
              <CountDown
                ref="formCount"
                :before-send="beforeSend"></CountDown>
            </div>
          </template>
        </VanField>
        <div class="submit" @click="submitForm">立即抢购</div>
      </VanForm>
    </div>
    <div class="line"></div>
    <RecentBuy2 />
    <img
      v-for="(v, k) in detailImg"
      :key="k"
      :src="require(`@/assets/Scene/Live12/${v}.png`)"
      alt="" />
    <BackDiaLog
      @confirm="pay"
      @cancel="stop"
      :is-live="true"
      v-model="showDialog" />
    <CopyRight />
    <PayTypeDialog @change="pay" v-model="showPaySelet"></PayTypeDialog>
    <Footer
      :local-key="localKey"
      @submit="submit"
      @submit199="submit199"
      @finish="changeSn"></Footer>
  </div>
</template>
<script>
import { getSearch } from '@/utils'
import BackDiaLog from '../components/StayDialog' // 支付挽留弹框
import PayTypeDialog from '@/components/common/SelectPayType' // 支付方式选择弹窗
import CountDown from '@/components/common/CountDown'
import Footer from '@/views/Scene/components/footer'
import RecentBuy2 from '@/components/RecentBuy2'
import CopyRight from '@/components/CopyRight'
import { codeMap } from './ErrorMsg'
export default {
  components: {
    BackDiaLog,
    PayTypeDialog,
    CountDown,
    Footer,
    RecentBuy2,
    CopyRight
  },
  data: function () {
    return {
      detailImg: ['detail1', 'detail2', 'detail3', 'detail4'],
      localKey: 'SceneLive12-time',
      sn: 'PRODUCT_SN_12_WM_LIVE',
      form: {},
      showPaySelet: false,
      showDialog: false,
      isShowCode: false // 是否显示验证码输入框
    }
  },
  async created () {
    this.form = {
      phone: localStorage.getItem('SceneLive12-phone'),
      code: localStorage.getItem('SceneLive12-code')
    }
    if (this.form.phone && this.form.code) this.isShowCode = true
    const needCheckOrder = getSearch('checkOrder')
    // 如果是支付返回的 则检查订单
    if (needCheckOrder) {
      this.changeQuery('checkOrder')
      // 倒计时不结束的时候显示
      if (Number(localStorage.getItem(this.localKey))) this.showDialog = true
      this.checkOrder()
      this.timer = setInterval(() => {
        this.checkOrder()
      }, 3000)
    }
  },

  beforeDestroy () {
    clearInterval(this.timer)
  },

  methods: {
    // 用户输入手机号
    iptPhone (p) {
      if (p.length >= 11 && !this.isShowCode) this.isShowCode = true
    },
    // 倒计时结束更换商品编号
    changeSn () {
      this.showDialog = false
      this.sn = 'PRODUCT_SN_199_WM_LIVE'
    },
    stop () {
      this.showDialog = false
      this.$log.click({
        current_page: location.href,
        module_ori: '弹窗【放弃优惠】按钮',
        module_part: '106_toufang_lm_0429'
      })
      clearInterval(this.timer)
    },
    // 中部表单提交
    submitForm () {
      this.$log.click({
        current_page: location.href,
        module_ori: '中部【立即抢购】按钮',
        module_part: '106_toufang_lm_0424'
      })
      this.$refs.form.submit()
    },
    // 提交表单199（立即报名）
    submit199 () {
      this.$log.click({
        current_page: location.href,
        module_ori: '底部【立即报名】按钮',
        module_part: '106_toufang_lm_0427'
      })
      this.$refs.form.submit()
    },
    // 提交表单（立即抢购）
    submit () {
      this.$log.click({
        current_page: location.href,
        module_ori: '底部【立即抢购】按钮',
        module_part: '106_toufang_lm_0426'
      })
      this.$refs.form.submit()
    },
    // 弹出提示
    showMsg (err) {
      const { msg, code } = err
      let confirmButtonText = '确定'
      if (err.code === 30006) {
        confirmButtonText = '查看分班'
      }
      this.$dialog({ message: msg || codeMap[code].msg, confirmButtonText }).then((res) => {
        if (code === 30006) {
          this.goSucccess()
        }
        // 未填手机号|手机号有误
        if ([33004, 33003].includes(Number(code))) {
          this.$refs.phone.focus()
        }
        // 未填验证码｜验证码有误
        if ([33002, 33005].includes(Number(code))) {
          this.$refs.code.focus()
        }
        this.$log.click(codeMap[err.code].report)
      })
    },
    // 跳转成功页面
    goSucccess () {
      this.$router.push({
        name: 'Officals',
        query: {
          ...this.$route.query
        }
      })
    },
    // 查询订单
    checkOrder () {
      if (!this.form.phone) return
      const medId = getSearch('medId')
      this.$apis.payment
        .checkOrder({
          phone: this.form.phone,
          product_sn: this.sn
        })
        .catch((err) => {
          // 支付成功后
          const code = err.code
          clearInterval(this.timer)
          this.showDialog = false
          if (code === 30006) {
            if (medId === '3610') {
              window._shTrackSendLog({ trackId: '19', trackType: 'PURCHASE', convertId: '9160' })
            }
            return this.goSucccess()
          }
          return this.showMsg(err)
        })
    },
    // 获取验证码
    beforeSend (start) {
      this.$log.click({
        current_page: location.href,
        module_ori: '【获取验证码】按钮',
        module_part: '106_toufang_lm_0425'
      })
      this.$refs.form
        .validate('phone')
        .then((res) => {
          start()
          this.$apis.userOperator.getPhoneCode({ phone: this.form.phone })
        })
        .catch((err) => {
          err = { code: err.message }
          this.showMsg(err)
        })
    },
    // 表单验证错误
    showErrorMsg (err) {
      err = { code: err.errors[0].message }
      this.showMsg(err)
    },
    // 表单校验失败
    onSubmit (e) {
      // 校验手机号和验证码
      localStorage.setItem('SceneLive12-phone', this.form.phone)
      localStorage.setItem('SceneLive12-code', this.form.code)
      this.$apis.payment
        .payBeforeCheck({
          ...this.form,
          product_sn: this.sn
        })
        .then(async (res) => {
          this.showPaySelet = true
        })
        .catch((err) => {
          this.showMsg(err)
        })
    },
    // 支付
    pay (payType) {
      this.showDialog = false
      this.$log.click({
        current_page: location.href,
        module_ori: '弹窗【立即抢购】按钮',
        module_part: '106_toufang_lm_0428'
      })
      if (payType) {
        localStorage.setItem('payType', payType)
      }
      payType = localStorage.getItem('payType')
      const query = getSearch()
      // let url = location.href
      // // 优惠价报名时有挽留弹窗
      // if (Number(localStorage.getItem(this.localKey)) !== 0) {
      //   url += '&checkOrder=yes'
      // }
      // const url = location.href + '&checkOrder=yes'
      const { origin, pathname, hash } = window.location
      const url = origin + pathname + hash + '&checkOrder=yes'
      this.$apis.payment
        .pay({
          ...query,
          ...this.form,
          pay_type: payType,
          return_url: encodeURIComponent(url),
          product_sn: this.sn
        })
        .then((data) => {
          if (payType === '1') {
            this.aliPay(data)
          }
          if (payType === '2') {
            this.wxPay(data)
          }
        })
        .catch((err) => {
          this.showMsg(err)
        })
    },
    // 微信支付
    wxPay (data) {
      window.location.replace(decodeURIComponent(data.url))
    },
    // 支付宝支付
    aliPay (data) {
      this.changeQuery('checkOrder', 'yes')
      const isReSet = document.getElementById('aLiPay')
      if (isReSet) {
        isReSet.parentNode.removeChild(isReSet)
      }
      const div = document.createElement('div')
      div.id = 'aLiPay'
      div.innerHTML = data
      document.body.appendChild(div)
      div.children[0].submit()
    },
    // 修改参数
    changeQuery (name, value) {
      const query = JSON.parse(JSON.stringify(this.$route.query))
      if (value) {
        query[name] = value
      } else {
        delete query[name]
      }
      this.$router.replace({
        ...this.$route,
        query: {
          ...query
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.gdt {
  padding-bottom: 106px;

  img {
    width: 100%;
    vertical-align: top;
  }

  .formPay {
    position: relative;
    width: 670px;
    margin: 25px auto 0 auto;
    box-sizing: border-box;
    padding: 30px 36px;
    text-align: center;
    background: #fff;
    border-radius: 8px 8px 0 0;
    // box-shadow: 0 -8px 20px 0 rgba(255, 75, 8, 0.2);

    .formTips {
      font-family: PingFangSC-Medium;
      font-size: 26px;
      color: #38465d;
      margin-bottom: 38px;
      text-align: center;
    }

    .submit {
      width: 600px;
      height: 88px;
      font-family: PingFangSC-Regular, PingFang SC;
      background: -webkit-linear-gradient(312deg, #ff5a00 0%, #ff3012 100%);
      background: -o-linear-gradient(312deg, #ff5a00 0%, #ff3012 100%);
      background: -moz-linear-gradient(312deg, #ff5a00 0%, #ff3012 100%);
      background: linear-gradient(312deg, #ff5a00 0%, #ff3012 100%);
      border-radius: 54px;
      font-size: 44px;
      font-weight: 400;
      color: #fff;
      line-height: 88px;
      text-shadow: 0 4px 2px rgba(227, 38, 0, 0.6);
      margin-bottom: 30px;
    }

    .code {
      display: inline-block;
      width: 229px;
      height: 100%;
      text-align: center;
      // border-left: rgba(0, 0, 0, 0.2) solid 1px;
      font-size: 32px;
      font-weight: 500;
      line-height: 32px;
      font-family: PingFangSC-Regular;
      color: #f3422f;
    }

    /deep/ .van-cell {
      .prefix {
        font-size: 32px;
        font-weight: 500;
        color: #2d2d2d;
        display: flex;
        align-items: center;

        .icon {
          font-size: 25px;
          margin-left: 10px;
          color: #b9b7b7;
        }

        padding-right: 15px;
      }

      margin-bottom: 20px;
      width: 600px;
      height: 86px;
      background: #f5f5f5;
      border-radius: 8px;
      border: 2px solid #ccc;
      font-size: 30px;
      font-family: SourceHanSansSC;
      font-weight: 500;
      color: #999;

      &::after {
        display: none;
      }
    }
  }

  .line {
    width: 652px;
    height: 2px;
    margin: 0 auto 50px;
    background: url('~@/assets/Scene/line.png') no-repeat top/contain;
  }
}
</style>
